import React, { Component } from "react";
import {
  Icon,
  SideBar,
  SideBarContent,
  SideBarItems,
  SideBarLink,
} from "./SidebarElements";

export class Sidebar extends Component {
  render() {
    return (
      <div>
        <SideBar isOpen={this.props.isOpen} onClick={this.props.toggle}>
          <Icon onClick={this.props.toggle}></Icon>

          <SideBarContent>
            <SideBarItems>
              <SideBarLink to="/tnp_demo" onClick={() => this.props.toggle}>
                Home
              </SideBarLink>
              <SideBarLink to="/about" onClick={this.props.toggle}>
                About
              </SideBarLink>
              <SideBarLink to="/contact" onClick={this.props.toggle}>
                Contact
              </SideBarLink>
              <SideBarLink to="/notice" onClick={this.props.toggle}>
                Notice
              </SideBarLink>
              <SideBarLink to="/recruitment" onClick={this.props.toggle}>
                Recruitment
              </SideBarLink>
              <SideBarLink to="/alumni" onClick={this.props.toggle}>
                Alumni
              </SideBarLink>
              <SideBarLink to="/placement" onClick={this.props.toggle}>
                Placements
              </SideBarLink>
              
              <SideBarLink to="/activities" onClick={this.props.toggle}>
                Activities
              </SideBarLink>
            </SideBarItems>
          </SideBarContent>
        </SideBar>
      </div>
    );
  }
}

export default Sidebar;
