import React from 'react'

const Alumni = () => {
    return (
      <>
        <div
          className="container text-center"
          style={{ backgroundColor: "#bca0dc" }}
        >
          <div className="container bg-white ">
            <h1 className="display-2">Welcome to our Alumni Section</h1>
            <nav class='shadow'>
              <div
                class="nav nav-tabs justify-content-center"
                id="nav-tab"
                role="tablist"
              >
                <button
                  class="nav-link active"
                  id="nav-mtech-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-mtech"
                  type="button"
                  role="tab"
                  aria-controls="nav-mtech"
                  aria-selected="true"
                >
                  M.Tech
                </button>
                <button
                  class="nav-link"
                  id="nav-btech-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-btech"
                  type="button"
                  role="tab"
                  aria-controls="nav-btech"
                  aria-selected="false"
                >
                  B.Tech
                </button>
                <button
                  class="nav-link"
                  id="nav-mdes-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-mdes"
                  type="button"
                  role="tab"
                  aria-controls="nav-mdes"
                  aria-selected="false"
                >
                  M.Des
                </button>
                <button
                  class="nav-link"
                  id="nav-bdes-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-bdes"
                  type="button"
                  role="tab"
                  aria-controls="nav-bdes"
                  aria-selected="false"
                >
                  B.Des
                </button>
                <button
                  class="nav-link"
                  id="nav-diploma-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-diploma"
                  type="button"
                  role="tab"
                  aria-controls="nav-diploma"
                  aria-selected="false"
                >
                  Diploma
                </button>
                <button
                  class="nav-link"
                  id="nav-phd-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-phd"
                  type="button"
                  role="tab"
                  aria-controls="nav-phd"
                  aria-selected="false"
                >
                  PhD
                </button>
              </div>
            </nav>
            <div class="tab-content" id="nav-tabContent">
              <div
                class="tab-pane fade show active"
                id="nav-mtech"
                role="tabpanel"
                aria-labelledby="nav-mtech-tab"
              >
                M.Tech
              </div>
              <div
                class="tab-pane fade"
                id="nav-btech"
                role="tabpanel"
                aria-labelledby="nav-btech-tab"
              >
                B.Tech
              </div>
              <div
                class="tab-pane fade"
                id="nav-mdes"
                role="tabpanel"
                aria-labelledby="nav-mdes-tab"
              >
                M.Des
              </div>
              <div
                class="tab-pane fade"
                id="nav-bdes"
                role="tabpanel"
                aria-labelledby="nav-bdes-tab"
              >
                B.Des
              </div>
              <div
                class="tab-pane fade"
                id="nav-diploma"
                role="tabpanel"
                aria-labelledby="nav-diploma-tab"
              >
                Diploma
              </div>
              <div
                class="tab-pane fade"
                id="nav-phd"
                role="tabpanel"
                aria-labelledby="nav-phd-tab"
              >
                PhD
              </div>
            </div>
          </div>
        </div>
      </>
    );
}

export default Alumni
