import React, { useState, useEffect } from "react";

const Notice = () => {
  const [data, setData] = useState();
  const getData = async () => {
    try {
      const res = await fetch(
        "https://sheet.best/api/sheets/cd012735-aa03-4afe-b86f-4f38cd11a3b7"
      );
      const data = await res.json();
      setData(data);
    } catch (error) {
      console.log("error");
    }
  };
  useEffect(() => {
    getData();
  }, []);
  const mystyle = {
    backgroundColor: "#3c1361",
    color: "white",
    padding: "7px",
    border: "none",
    borderRadius: "3px",
    outline: "none",
    textDecoration: "none!important",
  };

  return (
    <>
      <div
        className="container text-center"
        style={{ backgroundColor: "#bca0dc" }}
      >
        <div className="container shadow bg-white ">
          <h1 className="display-2">Notice</h1>

          {data
            ?.slice(0)
            .reverse()
            .map((item, i) => (
              <>
                {" "}
                <div class="jumbotron">
                  <h1 class="display-4">{item.title}</h1>
                  <p class="lead">{item.date}</p>
                  <hr class="my-4" />
                  <p>{item.description}</p>
                  <a
                    class="btn btn-light text-white"
                    style={mystyle}
                    target="_blank"
                    href={item.link}
                    role="button"
                  >
                    Link
                  </a>
                </div>
              </>
            ))}
        </div>
      </div>
    </>
  );
};

export default Notice;
