import React, { useState } from "react";

import Header from "../src/components/Header/Header";
import Sidebar from "../src/components/Sidebar/Sidebar";
import Hero from "../src/components/Hero/Hero";
import Footer from "../src/components/Footer/Footer";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import About from "../src/components/AboutSection/About";
import Contact from "../src/components/ContactSection/Contact";
import Placement from "../src/components/PlacementSection/Placement";
import Activities from "../src/components/ActivitiesSection/Activities";
import Notice from "../src/components/NoticeSection/Notice";
import Alumni from "../src/components/AlumniSection/Alumni";

const Home = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => [setIsOpen(!isOpen), console.log("works bro")];

  const propy = "pass";
  return (
    <Router>
      <div>
        <Header toggle={toggle} pass={propy} isOpen={isOpen}></Header>
        <Sidebar toggle={toggle} pass={propy} isOpen={isOpen}></Sidebar>
        <div className="content">
          <Switch>
            <Route  path="/tnp_demo">
              <Hero />
            </Route>
            <Route  path="/about">
              <About />
            </Route>
            <Route  path="/placement">
              <Placement />
            </Route>
            <Route  path="/contact">
              <Contact />
            </Route>
            <Route  path="/about">
              <About />
            </Route>
            <Route  path="/activities">
              <Activities />
            </Route>
            <Route  path="/notice">
              <Notice />
            </Route>

            {/* <Route exact path="/recruitment">
                <Activities />
              </Route> */}
            <Route exact path="/alumni">
              <Alumni />
            </Route>
          </Switch>
        </div>
        <Footer isOpen={isOpen}></Footer>
      </div>
    </Router>
  );
};

export default Home;
