import React, { Component, useEffect, useState} from "react";
// https://sheet.best/api/sheets/f5c596b9-1d37-4dbe-b183-65a8ed2a5b33
export const Placement=()=> {
  const [data, setData] = useState();
  const getData=async()=>{
    try {
      const res = await fetch('https://sheet.best/api/sheets/f5c596b9-1d37-4dbe-b183-65a8ed2a5b33');
      const data= await res.json();
      setData(data);
    } catch (error) {
      console.log('error');
    }
  }
  useEffect(()=>{getData()},[])
      
    return (
      <>
        <div
          className="container text-center"
          style={{ backgroundColor: "#bca0dc" }}
        >
          <div className="container shadow bg-white ">
            <h1 className="display-2">Latest Placement Record</h1>

            <div className="table-responsive">
              <table class="table bg-white">
                <thead>
                  <tr>
                    <th scope="col">Sl.No. </th>
                    <th scope="col">Name</th>
                    <th scope="col">Company</th>
                    <th scope="col">Designation</th>
                    <th scope="col">CTC</th>
                  </tr>
                </thead>

                <tbody id="demo">
                  {data?.map((item, i) => (
                    <>
                      {" "}
                      <tr>
                        <td >{item.Sl}</td>
                        <td>{item.Name}</td>
                        <td>{item.Company}</td>
                        <td>{item.Designation}</td>
                        <td>{item.CTC}</td>
                      </tr>
                    </>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </>
    );
  }
 




export default Placement;
